body {
  margin: 0;
  background-color: #f7dcf6;
  width: vh;
}
h1,
h2,
p {
  margin: 0;
}
span {
  margin: -4px;
}
ul,
li {
  list-style-type: none;
  padding-inline-start: 0px;
}
a {
  text-decoration: none;
}
@media screen and (max-width: 768px) {
  .header {
    background-color: #f7dcf6;
    display: flex;
    justify-content: space-between;
    padding: 16px 14px;
    color: #104d02;
    font-weight: 400;
    font-size: 24px;
    box-shadow: 0 5px 11px rgb(6 130 6 / 50%);
    margin-bottom: 12px;
    width: 100vh;
  }
  .img {
    background: transparent;
  }
  .headone {
    text-align: right;
    position: relative;
    right: 20px;
    font-weight: 400;
    font-size: 32px;
  }
  .mainpage {
    width: 100vh;
    text-align: center;
  }
  .divH1 {
    background-image: url('./src/images/image_860402161038407888179.gif');
    margin-bottom: 12px;
    color: #104d02;
    box-shadow: 0 5px 11px rgb(6 130 6 / 50%);
    width: 500px;
    height: 126px;
    padding-top: 30px;
  }
  .presentation {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-bottom: 20px;
  }
  .container {
    padding: 0 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vh;
  }
  .paragraf {
    border-radius: 10px;
    box-shadow: 0 7px 17px rgb(6 130 6 / 50%);
    padding: 10px;
    background-color: #f5abf2;
    width: 100vh;
  }
  .presentation-img {
    box-shadow: 0 5px 11px rgb(6 130 6 / 50%);
  }
  .par {
    font-weight: 400;
    font-size: 25px;
  }
  .work {
    display: flex;

    box-shadow: 0 5px 11px rgb(6 130 6 / 50%);
    padding-top: 10px;
    margin-bottom: 20px;
    justify-content: space-between;
  }
  .work-img {
    border-radius: 5px;
  }

  .work-h2 {
    position: relative;
    left: 30px;
  }
  .work-ul {
    width: 349px;
    font-weight: 400;
    font-size: 23px;
  }

  .minion {
    border-radius: 5px;
  }
  .time {
    display: flex;

    box-shadow: 0 5px 11px rgb(6 130 6 / 50%);
    padding: 10px;
    margin-bottom: 20px;
  }

  .footer {
    display: flex;
    justify-content: space-around;
    width: 100vh;
    padding: 16px 14px;
    color: #104d02;
    font-weight: 400;
    font-size: 23px;
    border-top: 1px solid #104d02;
  }
}
